*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "GameBoy";
  src: local("GameBoy"), url(./fonts/GameBoy.woff) format("woff");
}

* {
  font-family: "GameBoy", sans-serif;
  user-select: none;
  user-zoom: none;
}

body {
  position: fixed;
  padding: 0;
  margin: 0;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
  min-width: calc(var(--vw, 1vw) * 100);
  display: flex;
  background-color: #418ea4;
}

#root {
  display: flex;
  flex: 1;
}
